html {
  font-size: 16px;
}

body {
  margin: 0;
  padding: 0;
  background: #eee;
}

nav,
div,
input, form {
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 255;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.5);
  /* visibility: hidden;
  opacity: 0; */
  transition: 1s all ease;
}

.modal-active {
  visibility: visible;
  opacity: 1;
}

.login-form {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 90%;
  height: 400px;
  max-width: 300px;
  background: rgba(255,255,255,0.5);
  border-radius: 15px;
  box-shadow: 10px 10px 30px black;
}

.login-form h1 {
  margin: 30px;
  text-align: center;
  letter-spacing: 1px;
  color: white;
  text-shadow: 3px 3px 3px #000;
}

.login-form p {
  margin-left: 30px;
  color: rgba(255,255,255,1);
}

.input-box {
  margin: auto;
  margin-bottom: 15px;
  position: relative;
  width: 90%;
  text-align: center;
}

.input-box label {
  position: absolute;
  top: 50%;
  left: 25px;
  width: 25px;
  height: 25px;
  color: white;
  transform: translateY(-50%);
}

.input-box input{
  padding-left: 30px;
  width: 90%;
  height: 40px;
  font-size: 1.2rem;
  text-align: center;
  color: black;
  border: none;
  background: rgba(255,255,255,0.75);
}

.input-box input::placeholder {
  color: rgba(0,0,0,0.6);
}

.input-box input:focus {
  background: rgba(0,0,0,0.1);
  color: white;
}

.input-box button {
  margin-top: 20px;
  width: 90%;
  height: 40px;
  font-size: 1.2rem;
  text-align: center;
  color: white;
  font-weight: bold;
  letter-spacing: 1px;
  border: none;
  background: black;
}

.input-box button:nth-child(2) {
  margin: 10px;
  width: calc(45% - 10px);
  background: #2DB400
}

.input-box button:nth-child(3) {
  margin: 10px;
  width: calc(45% - 10px);
  background: #ffe812;
  color: black;
}

.nav {
  display: flex;
  flex-flow: row nowrap;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 254;
  width: 100%;
  height: 50px;
}

.nav-black {
  background: rgba(0, 0, 0, 1);
  color: rgba(255, 255, 255, 1);
}

.nav-white {
  background: rgba(255, 255, 255, 1);
  color: black;
}

.nav-item {
  margin: 0;
  padding: 15px;
}

.nav-item h4 {
  transition: .5s all;
}

.flex-grow {
  flex-grow: 2;
}

.section {
  position: relative;
  top: 50px;
  width: 100%;
  height: 100%;
}

.container {
  margin: auto;
  padding: 15px;
  width: 90%;
}

.flex-row-wrap {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
}

.flex-item-12 {
  margin: 5px 15px;
  width: calc(100% - 30px);
}

.flex-item-4 {
  margin: 5px 15px;
  padding: 15px;
  width: calc(25% - 30px);
}

.sort-group {
  text-align: right;
}

.sort-button {
  margin-right: 5px;
  padding: 5px 10px;
  font-size: 1rem;
  border: 1px solid #aaa;
  border-radius: 5px;
  text-align: center;
  background: rgba(0,0,0,1);
  color: white;
}

.sort-button:active {
  background: rgba(0,0,0,0.7);
}

.card {
  padding: 15px;
  border: 1px solid #ccc;
  border-radius: 15px;
  min-width: 300px;
  text-align: center;
  background: rgba(255, 255, 255, 0.7);
}

.card>img {
  width: 100%;
}

.card>h4 {
  margin-bottom: 15px;
  font-size: 1.2rem;
}

.card>p {
  margin-bottom: 15px;
  font-size: 1rem;
}

.card p:nth-of-type(2) {
  font-weight: bold;
  font-size: 1.1rem;
  letter-spacing: 1px;
}

.card-btngroup {
  width: 100%;
  text-align: right;
}

.card-btngroup >span {
  margin-right: 10px;
}